
import yheader from '../components/ycheader'
import Tab from 'vux/src/components/tab/tab.vue'
import TabItem from 'vux/src/components/tab/tab-item.vue'
import Flexbox from 'vux/src/components/flexbox/flexbox.vue'
import FlexboxItem from 'vux/src/components/flexbox/flexbox-item.vue'

import { mapState } from 'vuex'
export default {
name: 'myOrder',
data() {
return {
lists:[],

}
},
computed: {
...mapState({
showOrder: state => state.vux.showOrder
})
},
components: {
yheader,
Tab,
TabItem,
Flexbox,
FlexboxItem
},
created() {
this.getMyorder(0);//初始化申购订单
},
methods: {
onItemClick(index) {
this.getMyorder(index);
},
getMyorder(index){
let _this = this;
this.post({
url: "/fundOrder/fundOrderList/v1.0",
data:{
flag:index+1
},
success: function(e) {
if (e.code == "0000") {
_this.lists=e.result.list;
setTimeout(()=>{
_this.$store.commit('updateShowOrder', {showOrder: true})
},600);
}
}
})
}
}
}



